export var NucKinematics = (function(){
'use strict';
    
    //parameters
    var u = 931.494061; //mass unit in MeV/c^2
    var m_e = 0.510999; //electron mass in MeV/c^2 
    var m_e_amu = m_e/u; // electron mass in amu
    var c = 299.792458; //speed of light in Mm/s

    // beta(gamma)    
    var beta_from_gamma = function(g){
        if(g<1)return -1;
        return Math.sqrt(1-(1/(g*g)));
    };
    
    // gamma(beta)
    var gamma_from_beta = function(b){
        if(b<0 || b>1) return -1;
        //if(b==1)return Infinity;
        if(b==1)return -1;
        return 1/Math.sqrt(1-(b*b));
    };

    // T from gamma
    var T_from_gamma = function(g){
        if(g<1)return -1;
        return u * (g-1);
    };
    
    // T from beta
    var T_from_beta = function(b){
        return T_from_gamma(gamma_from_beta(b));
    };
    
    //gamma from T
    var gamma_from_T = function(t){
        if(t<0)return -1;
        return (t+u)/(u);
    };
    
    //beta from T
    var beta_from_T = function(t){
        return beta_from_gamma(gamma_from_T(t));
    };
    
    //Momentum from beta
    var p_from_beta = function(b,m_a){
        if(b<0 || b>=1)return -1;
        if(m_a<1)return -1;
        
        return m_a * u * gamma_from_beta(b)*b;
    };

    var p_from_T = function(T, M){
        return M*Math.sqrt( (T*T)  + (2.0*T*u));
    };
    
    //Beta from Momentum
    var beta_from_p = function(p,m_a){
        if(p<0)return -1;
        if(m_a<1)return -1;
        return p/(Math.sqrt( (m_a*u*m_a*u)+(p*p)));
    };
    
    
    //Brho from beta
    var brho_from_beta = function(b,m_a,q){
        if(b<0 || b>=1)return -1;
        if(m_a<1)return -1;
        if(q<1)return -1;
        
        return m_a * u * gamma_from_beta(b)*b/q/c;
    };
    
    //Beta from Momentum
    var beta_from_brho = function(brho,m_a,q){
        if(brho<0)return -1;
        if(m_a<1)return -1;
        if(q<1)return -1;
        if(m_a<q)return -1;
        return brho*q*c/(Math.sqrt( (m_a*u*m_a*u)+(brho*brho*q*q*c*c)));
    };
    
    
    //Brho from T
    var brho_from_T = function(T, m_a,q){
        if(T<0)return -1;
        var b = beta_from_T(T);
        if(b<0)return -1;
        return brho_from_beta(b,m_a,q);
        };
        
    //TOF from path and beta, path in m, TOF in ns
    var tof_from_beta = function(b,s){
        if(s<0)return -1;
        if(b<0 || b >=1)return -1;
       return 1000*s/b/c;
    };
    
    //Cherenkov threshold from beta
    var nth_from_beta = function(b){
        if(b<0 || b>=1)return -1;
        return 1/b;
    };
    
    //Cherenkov threshold from beta
    var beta_from_nth = function(n){
        if(n<1)return -1;
        return 1/n;
    };
    
    var relative_momentum_deviation = function(sigma_E, E){
    var res = 0;
    if(E>0){
        var g = gamma_from_T(E);
        res = g*sigma_E/E/(g+1);
        }
    return res;
    };
        
    // return public methods
    return {
        beta_from_gamma : beta_from_gamma,
        gamma_from_beta : gamma_from_beta,
        T_from_gamma : T_from_gamma,
        T_from_beta : T_from_beta,
        beta_from_T : beta_from_T,
        gamma_from_T : gamma_from_T,
        p_from_beta: p_from_beta,
        p_from_T : p_from_T,
        beta_from_p: beta_from_p,
        brho_from_beta: brho_from_beta,
        beta_from_brho: beta_from_brho,
        brho_from_T: brho_from_T,
        tof_from_beta: tof_from_beta,
        nth_from_beta: nth_from_beta,
        beta_from_nth: beta_from_nth,
        relative_momentum_deviation:relative_momentum_deviation,
        c:c,
        u:u,
        m_e:m_e,
        m_e_amu:m_e_amu
    };

})();
