import {NucData} from "./nucdata";
import {NucID} from "./elements";
import {NucKinematics} from "./kinematics";

function CalculatorState(){
    this.A = 1;
    this.Z = 1;
    this.Q = 1;
    this.mA = 1.00728;
    this.AoQ = 1.00728;
    this.Symbol = "1H";
    
    this.T = 0;
    this.Beta=0;
    this.Gamma=1;
    this.Brho=0;
    this.P = 0;
    this.Path=0;
    this.TOF=0;
    this.Nth=NaN;
    
    this.lastchanged = -1;
}


CalculatorState.prototype.SetNuclei = function(symbol){
    var res = NucID.GetNuc(symbol);
    if(res[0]>0 && res[1]>0){
        this.A = res[0];
        this.Z = res[1];
        this.Q = this.Z;
        this.Symbol = NucID.GetSymbol(this.A,this.Z);
        return true;
    }
    return false;
};
    
CalculatorState.prototype.GetNucleiData = function(){
    var res = NucData.GetNucData(this.A,this.Z,this.Q);
    if(res.hasOwnProperty('MN')){
        this.mA = res.MN;
        }
    else{
        this.mA = this.A;
        }
};

CalculatorState.prototype.Valid = function(){
    if(this.A<1 || this.Z<1 || this.Q<0){return false;}
    if(this.A<this.Z){return false;}
    if(this.Q>this.Z){return false;}
    return true; 
};
    

export function SharpCalculator(){
    var state = new CalculatorState();
    
    var changed = {
        A:1,
        Z:2,
        Q:3,
        Symbol:4,
        T:5,
        Beta:6,
        Gamma:7,
        Brho:8,
        P:9,
        Path:10,
        Nth:11,
        Nucleus:12
        };
    
    // Set new calculator nucleus from given symbol
    // return false if failed or true is updated
    var SetNucleusSymbol =  function(symbol){
        var res = NucID.GetNuc(symbol);
        if(res[0]>0 && res[1]>0){
            state.A = res[0];
            state.Z = res[1];
            state.Q = res[1];
            state.Symbol = NucID.GetSymbol(state.A,state.Z);
			state.GetNucleiData();
			ChangedNucleusUpdate();
            return true;
        }
        else{
            return false;
            }
        };
    
	// update nuclei with a,z,q combination
	// return true if ok, false if failed
	var SetNucleus = function(a,z,q){
		if(q===undefined){
			q=z;
			}
		if(a<1 || z<1 || q<0){return false;}
		if(a<z){return false;}
		if(q>z){return false;}
		
		state.A = a;
		state.Z = z;
		state.Q = q;
		state.Symbol = NucID.GetSymbol(state.A,state.Z);
		state.GetNucleiData();
		ChangedNucleusUpdate();
		return true;
		};
	
    var GetNucleus = function(){
        return [state.A, state.Z, state.Q];
    };
	
	var GetNucleusSymbol = function(){
		return NucID.GetSymbol(state.A,state.Z);
    };
	
    
    var ChangedNucleusUpdate = function(){
        state.AoQ = state.mA / state.Q;
        state.P = NucKinematics.p_from_beta(state.Beta,state.mA);
        state.Brho = NucKinematics.brho_from_beta(state.Beta,state.mA,state.Q);
    };
    
      
	//update kinematics parameters and recalculate others accordingly
	//functions should check valid values
	//returns false if failure, true on success
    var Set = {
        
            T : function(_T){
					if(_T<0){
						return false;
						}
                    
					state.T = _T;
                    state.Gamma = NucKinematics.gamma_from_T(state.T);
                    state.Beta = NucKinematics.beta_from_gamma(state.Gamma);
                
                    state.Nth = NucKinematics.nth_from_beta(state.Beta);
                    state.TOF = NucKinematics.tof_from_beta(state.Beta,state.Path);
                    state.Brho = NucKinematics.brho_from_beta(state.Beta,state.mA,state.Q);
                    state.P = NucKinematics.p_from_beta(state.Beta,state.mA);
					return true;
                },
                
            Beta: function(_b){
					if(_b<0 || _b>=1){
						return false;
						}
                    state.Beta = _b;
                    state.Gamma = NucKinematics.gamma_from_beta(state.Beta);
                    state.T = NucKinematics.T_from_gamma(state.Gamma);
                
                    state.Nth = NucKinematics.nth_from_beta(state.Beta);
                    state.TOF = NucKinematics.tof_from_beta(state.Beta,state.Path);
                    state.Brho = NucKinematics.brho_from_beta(state.Beta,state.mA,state.Q);
                    state.P = NucKinematics.p_from_beta(state.Beta,state.mA);
					return true;
                },
                
            Gamma: function(_g){
					if(_g<1){
						return false;
						}
                    state.Gamma = _g;
                    state.Beta = NucKinematics.beta_from_gamma(state.Gamma);
                    state.T = NucKinematics.T_from_gamma(state.Gamma);
                
                    state.Nth = NucKinematics.nth_from_beta(state.Beta);
                    state.TOF = NucKinematics.tof_from_beta(state.Beta,state.Path);
                    state.Brho = NucKinematics.brho_from_beta(state.Beta,state.mA,state.Q);
                    state.P = NucKinematics.p_from_beta(state.Beta,state.mA);
                    return true;
                },
            
            Brho: function(_b){
					if(_b<0){
						return false;
						}
                    
                    state.Brho = _b;
                    state.Beta = NucKinematics.beta_from_brho(state.Brho,state.mA,state.Q);
                
                    state.Gamma = NucKinematics.gamma_from_beta(state.Beta);
                    state.T = NucKinematics.T_from_gamma(state.Gamma);
                
                    state.Nth = NucKinematics.nth_from_beta(state.Beta);
                    state.TOF = NucKinematics.tof_from_beta(state.Beta,state.Path);
                    state.P = NucKinematics.p_from_beta(state.Beta,state.mA);
                    return true;
                },
                
            P: function(_p){
					if(_p<0){
						return false;
						}
                    
                    state.P = _p;
                    state.Beta = NucKinematics.beta_from_p(state.P,state.mA);
                
                    state.Gamma = NucKinematics.gamma_from_beta(state.Beta);
                    state.T = NucKinematics.T_from_gamma(state.Gamma);
                
                    state.Nth = NucKinematics.nth_from_beta(state.Beta);
                    state.TOF = NucKinematics.tof_from_beta(state.Beta,state.Path);
                    state.Brho = NucKinematics.brho_from_beta(state.Beta,state.mA,state.Q);
                    return true;
                },
                
            Path: function(_p){
                if(_p<0)return false;
                state.Path = _p;
                state.TOF = NucKinematics.tof_from_beta(state.Beta,state.Path);
				return true;
                },
                
            Nth: function(_n){
                if(_n<=1)return false;
                state.Nth = _n;
                state.Beta = NucKinematics.beta_from_nth(state.Nth);
                state.Gamma = NucKinematics.gamma_from_beta(state.Beta);
                state.T = NucKinematics.T_from_gamma(state.Gamma);
                
                state.TOF = NucKinematics.tof_from_beta(state.Beta,state.Path);
                state.Brho = NucKinematics.brho_from_beta(state.Beta,state.mA,state.Q);
                state.P = NucKinematics.p_from_beta(state.Beta,state.mA);
				return true;
                },
                
            A: function(_a){
				if(SetNucleus(_a,state.Z,state.Q)){
					return true;
					}
				else{
					return false;
					}
                },
			mA: function(_m){
				if(_m<=0){
					return false;
					}
				else{
					state.mA = _m;
					ChangedNucleusUpdate();
					return true;
					}
				},
				
            Z:  function(_z){
					if(SetNucleus(state.A,_z)){
						return true;
						}
					else{
						return false;
						}
                },
                            
            Q:  function(_q){
				if(SetNucleus(state.A,state.Z,_q)){
					return true;
					}
                else{
					return false;
					}
                },
                
            Symbol: function(s){
                return SetNucleusSymbol(s);
                }
            
            };
    
    return {
        state:state,
		Get:state,
        SetNucleusSymbol:SetNucleusSymbol,
		SetNucleus:SetNucleus,
        GetNucleus:GetNucleus,
		GetNucleusSymbol:GetNucleusSymbol,
        Set: Set,
    };


}



