export function epax(Ap, Zp, At, Zt, A, Z){
    var s  = [0.27,1.8];    // scaling in barn
    var p  = [-1.731,-0.01399];
    var y = [0.75,0.1];
    var d1 =-1.087;
    var d2 = 3.047E-2;
    var d3 = 2.135E-4;
    var d4 = 71.35;
    var d5 =-25.0;
    var d6 = 0.8;
    
    var n1 = 0.4;
    var n2 = 0.6;
    
    var q1 = -10.25;
    var q2 =  10.25;
    
    var r = [2.78, -0.015, 3.2e-5, 0.0412, 0.124, 30.0, 0.85];
    
    var Un = 1.65;
    var Up = 2.1;
    
    var l1 = 1.2;
    var l2 = 0.647;
    
    var b1 = 2.3e-3;
    var b2= 2.4;

    var Zprob,Zbeta, Zbeta_p;

    var R,R0,zp_diff,delta,delta_m;
    var expo,fract,yield_a,fwhm;
    var norm;
    var result;
    result = -1.0;
  
    var Ap13 = Math.pow(Ap,1.0/3.0);
    var At13 = Math.pow(At,1.0/3.0);

      /* calculate mass yield: */

  /* slope parameter */
  var P = Math.exp(p[0]+p[1]*Ap) ;
  var S = s[0]*P*(Ap13 +At13-s[1]) ;
  yield_a = S * Math.exp(-P * (Ap - A)) ;
  var mass_ratio = A/Ap;

  if ( mass_ratio > y[0]) {
    yield_a *= Math.exp(y[1]*Ap*Math.pow( (mass_ratio)-y[0],2)) ;
  }

  // centroid Zprob
  Zbeta = A/(1.98+0.0155*Math.pow(A,2.0/3.0)) ;  // Eq (7)
  Zbeta_p = Ap/(1.98+0.0155*Math.pow(Ap,2.0/3.0));

  if(A > d4) {  // Eq (8)
    delta = d1+d2*A ;
 }
 else
 {
    delta = d3*A*A ; 
 } 

 if(mass_ratio > d6) {  // Eq. (9)
    delta = delta*( 1 + (d5*Math.pow(mass_ratio-d6,2)) );
  }

  zp_diff = Zp - Zbeta_p ;

  if((zp_diff) > 0) {  /* ------------- p-rich */
    delta_m = Math.exp(q1+q2*mass_ratio)* zp_diff ;
 }
 else                /* ------------- n-rich */
 {
   var mass_ratio2 = mass_ratio*mass_ratio;
    delta_m = mass_ratio2*(n1+(n2*mass_ratio2))*zp_diff ;
 }

 Zprob = Zbeta + delta + delta_m + 0.002*A; // Eq (6) from errata
  /* calculate width parameter */
  if ((zp_diff)<0) {           /* n-rich */
     R0 = r[0] * Math.exp(r[3]*zp_diff) ; // Eq (13)
  }
  else  
  {                               /* p-rich */
     R0 = r[0] * Math.exp(r[4]*zp_diff) ; //Eq (14)
  }   
  R = R0 * Math.exp(r[1]*A + r[2]*A*A) ; // Eq (12)

  if (mass_ratio > r[6]) {
    R *= Math.exp(r[5] * Math.sqrt(Ap) * Math.pow(mass_ratio-r[6],3.0)) ; //Eq (15)
  }

  if((Zprob-Z) > 0) {
    /*     neutron-rich */
      expo = -R * Math.pow(Math.abs(Zprob-Z),Un) ;
      fract = Math.exp(expo) * Math.sqrt(R/3.14159) ;
    }
    else {
    /* old V2.1 parameterization */
      var slope = l1 + l2 * Math.pow(A/2.0,0.3) ;
      var z0 = Zprob+( slope*Math.log(10.)/(2.*R)) ;
      expo = -R * Math.pow(Math.abs(Zprob-Z),Up) ;
      fract   =  Math.exp(expo) * Math.sqrt(R/3.14159) ;
      if( Z > z0 ) {
        expo  = -R * Math.pow(Math.abs(Zprob-z0),Up) ;
        var  a2    =  Math.exp(expo) * Math.sqrt(R/3.14159) ;
        /* fract =  a2 * exp(slope*(z0-Z)) */
        fract = a2/Math.pow( Math.pow(10,slope),(Z-z0) ) ;
      }
    }

    fwhm = Math.pow(0.693/R,1.0/Un) + Math.pow(0.693/R,1.0/Up) ;
  norm = 1.0 ;

  /* "brute force" scaling factor for n-rich projectiles only */

  if((zp_diff)<=0) { 
    if ((Zbeta-Z)>(b2+zp_diff)) { 
      norm = Math.pow(10.0, -b1 * Math.abs(zp_diff) * Math.pow(Zbeta-Z+Zp - Zbeta_p + b2,3)) ;
    }
    else
    {
      norm=1.0 ;
    }
  }

  fwhm = Math.pow(0.693/R,1.0/Un) + Math.pow(0.693/R,1.0/Up) ; 
  result =  norm * fract * yield_a ; 
  return(result*1000);
}


export function goldhaber(Ap, Af){
  return 90.0*Math.sqrt(Af*(Ap-Af)/(Ap-1));
}

