        // create symbol-> proton number table
        var e;
        var elements_symbol_z = {};
        for(e = 1; e< elements_z_symbol.length;e++){
            elements_symbol_z[elements_z_symbol[e]]=e;
            }
        

    // Get Element Z number from element symbol
    var  GetElZ = function(str){
        // empty string check
        if(str.length<1){return -1;}

            str = str[0].toUpperCase()+str.slice(1);

        if(!elements_symbol_z[str]){
            return -1;
        }
        return elements_symbol_z[str];
    };
    
    
    //Get element Symbol from Z number
    var GetElSymbol = function(z){
        if(z<0 || z>118){return "";}
        return elements_z_symbol[z];
    };
    
    
    var GetSymbol = function(a,z){
        if(z<0 || z>118){return "";}
        if(a==0){
            return elements_z_symbol[z];
        }
        if(a<z){return "";}
        return a+elements_z_symbol[z];
        };

    var GetNuc = function(str){
        
        var _a = -1;
        var _z = -1;
        var _q = -1;
        
        if(!str || str.length < 1){
            return [-1,-1]; 
            }
        var re = /(\d{1,3})?([A-z]{1,3})(\d{1,3})?([+])?/;
        var result = re.exec(str);
        
        if(result===null){return [-1,-1];}
        _z = GetElZ(result[2]);
        if(result[1]===undefined){
	    _a = 0;
	}
	else{
    	    _a = parseInt(result[1]);
        }
        
        if(result[3] && (!result[4])){
    	return [-1,-1];
        }
        // check parsed A,Z numbers
        
        if(_z==-1){return [-1,-1];}
        if( isNaN(_a)){return [-1,-1];}
        if(_a === 0){return [0,_z];}
        if(_a>=_z){return [_a,_z];}
        
        //default return
        return [-1,-1];
        
               
    };


    // return public methods
    return {
        GetNuc: GetNuc,
        GetElementZ: GetElZ,
        GetElementSymbol: GetElSymbol,
        GetSymbol: GetSymbol
    };


