
function band_matrix(dim){
	this.a = new Float32Array(dim);
	this.d = new Float32Array(dim);
	this.c = new Float32Array(dim);
	this.dim = dim;
	this.at = function(i,j){
		var k = j - i;
		if(k>2 || k<-2)return;
		if(k==1)return this.c[i];
		else if(k==0)return this.d[i];
		else return this.a[i];
		};

	this.set = function(i,j,val){
		var k = j - i;
		if(k>2 || k<-2){
			console.log("wrong diagonal");
			return;
			}
		if(k==1)this.c[i] = val;
		else if(k==0)this.d[i] = val;
		else this.a[i] = val;
		};
	
	this.trig_solve = function(b){
		if(!b || b.length!=this.d.length)return;
		if(this.d[0]==0.0){
			console.log("diagonal element is zero at 0");
			return;
			}
		var x = new Float32Array(this.dim);
		var g = new Float32Array(this.dim);
		x[0] = b[0]/this.d[0];
		var bet = this.d[0];
		for(var j=1;j<this.dim;j++){
			g[j] = this.c[j-1]/bet;
			bet = this.d[j] - (this.a[j]*g[j]);
			if(bet == 0.0){
				console.log("diagonal element is zero at "+ j);
				return;
				}
			x[j] = (b[j]-this.a[j]*x[j-1])/bet;
			}
		for(var k=this.dim-2;k>=0;k--){
			x[k] -= g[k+1]*x[k+1];
			}
		return x;
		};
	}
	

export function spline(x,y){
	if(!x || !y || x.length !=y.length || x.length<3)return;
	this.x = x;
	this.y = y;
	this.n = x.length;
	this.m_a  = new Float32Array(this.n);
	this.m_b  = new Float32Array(this.n);
	this.m_c  = new Float32Array(this.n);
	this.m_left_value = 0.0;
	this.m_right_value = 0.0;
	this.m_left = 1;
	this.m_right = 1;
	this.m_b0 = 0.0;
	this.m_c0 = 0.0;
	
	this.set_points = function(){
		var A = new band_matrix(this.n);
		var rhs = new Float32Array(this.n);
		var i;
		for(i=1; i<this.n-1; i++) {
			A.set(i,i-1,1.0/3.0*(this.x[i]-this.x[i-1]));
			A.set(i,i,2.0/3.0*(this.x[i+1]-this.x[i-1]));
			A.set(i,i+1,1.0/3.0*(this.x[i+1]-this.x[i]));
			rhs[i]=(this.y[i+1]-this.y[i])/(this.x[i+1]-this.x[i]) - (this.y[i]-this.y[i-1])/(this.x[i]-this.x[i-1]);
		}
        
        // boundary conditions
            // 2*b[0] = f''
            A.set(0,0,2.0);
            A.set(0,1,0.0);
            rhs[0]=this.m_left_value;
            A.set(this.n-1,this.n-1,2.0);
            A.set(this.n-1,this.n-2,0.0);
            rhs[this.n-1]=this.m_right_value;
            this.m_b=A.trig_solve(rhs);
	    for(i=0; i<this.n-1; i++) {
                this.m_a[i]=1.0/3.0*(this.m_b[i+1]-this.m_b[i])/(this.x[i+1]-this.x[i]);
                this.m_c[i]=(this.y[i+1]-this.y[i])/(this.x[i+1]-this.x[i])-(1.0/3.0)*(2.0*this.m_b[i]+this.m_b[i+1])*(this.x[i+1]-this.x[i]);
		}
        
        // for left extrapolation coefficients
		this.m_b0 = this.m_b[0];
		this.m_c0 = this.m_c[0];

		// for the right extrapolation coefficients
		// f_{n-1}(x) = b*(x-x_{n-1})^2 + c*(x-x_{n-1}) + y_{n-1}
		var h=x[this.n-1]-x[this.n-2];
		// m_b[n-1] is determined by the boundary condition
		this.m_a[this.n-1]=0.0;
		this.m_c[this.n-1]=3.0*this.m_a[this.n-2]*h*h+2.0*this.m_b[this.n-2]*h+this.m_c[this.n-2];   // = f'_{n-2}(x_{n-1})
		};
	
	this.set_points();
	
	this.at = function(xx){
        var it=-1;
        for(var i=0;i<this.x.length;i++){
            if(this.x[i]>=xx){
                it = i;
                break;
                }
            }
		if(it>0)it--;
		if(it==-1){
			it=this.n-1;
		}
		if(it<0 || it>=this.n){
			console.log("wrong index: "+it);
		}
		var mx = this.x[it];
		var h=xx-mx;
		var interpol;

		if(xx<this.x[0]) {
			// extrapolation to the left
			interpol=(this.m_b0*h + this.m_c0)*h + this.y[0];
		} else if(xx>this.x[this.n-1]) {
        // extrapolation to the right
			interpol=(this.m_b[this.n-1]*h + this.m_c[this.n-1])*h + this.y[this.n-1];
		} else {
        // interpolation
        interpol=((this.m_a[it]*h + this.m_b[it])*h + this.m_c[it])*h + this.y[it];
		}
		return interpol;
		};
	
	this.deriv = function(xx){
        var it=-1;
        for(var i=0;i<this.x.length;i++){
            if(this.x[i]>=xx){
                it = i;
                break;
                }
            }
            
		if(it!=0)it--;
		var mx = this.x[it];
		var h=xx-mx;
		var interpol;
		
		if(xx<this.x[0]) {
			// extrapolation to the left
            interpol=2.0*this.m_b0*h + this.m_c0;
			} 
		else if(xx>this.x[this.n-1]){
        // extrapolation to the right
            interpol=2.0*this.m_b[this.n-1]*h + this.m_c[this.n-1];
            } 
        else {
        // interpolation
          interpol=(3.0*this.m_a[it]*h + 2.0*this.m_b[it])*h + this.m_c[it];
         }
    return interpol;
		};
	
	
	}
